.circle {
  width: 100%;
  height: 100%;
  border: 10px solid green;
  border-left: none;
  border-right: none;
  border-radius: 50%;
  position: absolute;
}

.circ1 {
  border-color: red;
  animation: circ1 1.5s linear -1s infinite;
}

.circ2 {
  border-color: #000;
  animation: circ2 1.5s linear -.5s infinite;
}

.circ3 {
  border-color: #ff0;
  animation: circ3 1.5s linear infinite;
}

@keyframes circ1 {
  0% {
    transform: rotateX(45deg) rotateY(45deg) rotateZ(0);
  }

  100% {
    transform: rotateX(45deg)rotateY(45deg)rotateZ(360deg);
  }
}

@keyframes circ2 {
  0% {
    transform: rotateX(60deg) rotateY(0deg) rotateZ(0);
  }

  100% {
    transform: rotateX(60deg)rotateY(0deg)rotateZ(360deg);
  }
}

@keyframes circ3 {
  0% {
    transform: rotateX(-45deg) rotateY(45deg) rotateZ(0);
  }

  100% {
    transform: rotateX(-45deg)rotateY(45deg)rotateZ(360deg);
  }
}

.loader:before {
  content: "";
  width: 10px;
  height: 10px;
  filter: blur(3px);
  background-color: #00f;
  border-radius: 50%;
  animation: blur 1s linear infinite;
  position: absolute;
  top: calc(50% - 5px);
  left: calc(50% - 5px);
}

@media screen and (min-width: 480px) {
  .loader:before {
    width: 20px;
    height: 20px;
    top: calc(50% - 10px);
    left: calc(50% - 10px);
  }
}

@media screen and (min-width: 768px) {
  .loader:before {
    width: 30px;
    height: 30px;
    top: calc(50% - 15px);
    left: calc(50% - 15px);
  }
}

@keyframes blur {
  50% {
    box-shadow: 0 0 10px 10px #00f;
  }

  100% {
    box-shadow: 0 0 10px 10px #00f;
  }
}

